<template>
  <div>
    <div>
      <!-- content -->
      <div id="content">
        <div id="g-content">
          <div id="content-title">
            <span class="title">รายการ</span>
          </div>
          <div id="content-r">
            <div id="content-title">
              <span class="select">เลือก {{select_count}}</span>
              <i
                class="glyphicon glyphicon-saved"
                id="Cicon"
                data-toggle="modal"
                data-target="#exampleModal"
              ></i>
            </div>
          </div>
        </div>

        <div id="css-underline"></div>

        <div id="sale-content">
          <div class="dropdown">
            <button class="btn btn-primary dropdown-toggle" type="button" data-toggle="dropdown">
              เลือกประเภท
              <span class="caret"></span>
            </button>
            <ul class="dropdown-menu">
              <li>
                <a href="#">HTML</a>
              </li>
              <li>
                <a href="#">CSS</a>
              </li>
              <li>
                <a href="#">JavaScript</a>
              </li>
            </ul>

            <!-- <table id="sale_table">
            <col width="75%">
            <col width="20%">
            <col width="5%">
            <thead>
              <tr>
                <th>รายการ</th>
                <th>ราคา/บาท</th>
                <th>กิจกรรม</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(item, i) in listorder.data" :key="i">
                <td>{{item.orders}}</td>
                <td>{{item.prices}}</td>
                <div id="b-active">
                  <button class="select" @click="select_order(item)">เลือก</button>
                </div>
              </tr>
            </tbody>
            </table>-->
            <div class="grid-container">
              <div class="grid-item" v-for="(item, i) in listorder.data" :key="i">
                <div class="grid-content">
                  <img class="img-menu"
                    src="https://i.ytimg.com/vi/mwRDE8KVWbM/maxresdefault.jpg"
                    alt="Paris"
                  >
                  <br><br>
                  <span>{{item.orders}}</span>
                  <br>
                  <span>ราคา {{item.prices}} บาท</span>
                  <span class="span-order" @click="select_order(item)">สั่งเมนูนี้</span>
                  <!-- <button class="select" @click="select_order(item)">เลือก</button> -->
                </div>
              </div>
            </div>
          </div>
        </div>
        <!-- Modal -->
        <div
          class="modal fade"
          id="exampleModal"
          tabindex="-1"
          role="dialog"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
        >
          <div class="modal-dialog" role="document">
            <div class="modal-content">
              <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
                <h5 class="modal-title" id="exampleModalLabel">Modal title</h5>
              </div>
              <div class="modal-body">
                <table id="sale_table">
                  <col width="75%">
                  <col width="20%">
                  <col width="5%">
                  <thead>
                    <tr>
                      <th>รายการ</th>
                      <th>ราคา/บาท</th>
                      <th>ยกเลิก</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="(item, b) in orderchecked" :key="b">
                      <td>{{item.cked_orders}}</td>
                      <td>{{item.cked_prices}}</td>
                      <div id="b-active">
                        <span class="order-cancel">&times;</span>
                      </div>
                    </tr>
                  </tbody>
                  <tbody>
                    <tr>
                      <td>ราคารวม</td>
                      <td>{{sum}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
              <div class="modal-footer">
                <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" class="btn btn-primary">Save changes</button>
              </div>
            </div>
          </div>
        </div>
      </div>
      <!-- content -->
    </div>
  </div>
</template>

<script>
export default {
  name: "saleorder",
  components: {},
  data() {
    return {
      listorder: [],
      select_count: 0,
      orderchecked: [],
      sum: 0
    };
  },
  methods: {
    itemList: function() {
      this.$http
        .get("http://localhost:1112/ezapi/api/get/getorder.php")
        .then(response => {
          //console.log(Response.body);
          this.listorder = response.body;
          //console.log(this.listorder);
        });
    },
    select_order: function(item) {
      this.select_count++;
      document.getElementById("Cicon").style.visibility = "visible";
      this.orderchecked.push({
        cked_orders: item.orders,
        cked_prices: item.prices
      });

      var num = +item.prices;
      this.sum += num;
      //console.log((this.sum));
    }
  },
  created: function() {
    this.itemList();
  }
};
</script>

<style scoped>
#content {
  padding-top: 15px;
  padding-left: 5%;
  padding-right: 5%;
  padding-bottom: 15px;
  width: 100%;
  height: max-content;
}

#g-contetn {
  width: 100%;
  height: max-content;
  background: red;
  padding: 15px;
}

#content-title {
  float: left;
  background: white;
  height: 50px;
  line-height: 50px;
  width: 50%;
  padding-left: 15px;
  padding-right: 15px;
}

span.title {
  font-size: 18px;
}

#content-r {
  width: 100%;
  text-align: right;
}

span.select {
  font-size: 18px;
  color: #00acc1;
}

#css-underline {
  margin-top: 50px;
  width: 100%;
  background: rgb(212, 212, 212);
  height: 2px;
}

#sale-content {
  padding: 10px;
  width: 100%;
  background: white;
  height: max-content;
}

.glyphicon-saved {
  font-size: 20px;
  margin-left: 10px;
  color: #00acc1;
  visibility: hidden;
  cursor: pointer;
  /* cornflowerblue */
}

.dropdown .btn-primary {
  margin-top: 5px;
  background-color: #00acc1;
  margin-bottom: 15px;
}

#sale_table {
  top: 10px;
  /* border: 1px solid #ddd; */
  background: white;
  width: 100%;
  border-collapse: collapse;
}

#sale_table th {
  background-color: #ddd;
  color: rgb(77, 76, 76);
  padding-bottom: 12px;
  padding-top: 12px;
  text-align: left;
}
#sale_table td {
  padding: 10px;
  /* border: 1px solid #ddd; */
}
#sale_table th {
  padding: 10px;
}
#sale_table tr:nth-child(even) {
  background-color: #f2f2f2;
}
#sale_table tr:hover {
  background-color: #ececec;
}

#b-active {
  width: 100%;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 10px;
}

button.select {
  width: 80%;
  height: 36px;
  padding: 6px 12px;
  color: #fff;
  background-color: #00acc1;
  border-color: rgb(26, 83, 189);
  border: 1px solid rgb(47, 102, 204);
  border-radius: 4px;
}

.modalss {
  width: 500px;
  height: 300px;
  background: #888888;
}

.grid-container {
  padding: 10px;
  width: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
}
.grid-item {
  width: 100%;
  background-color: rgba(255, 255, 255, 0.8);
  font-size: 16px;
  text-align: center;
}

.grid-content {
  font-size: 16px;
  margin: 20px;
  width: fit-content;
  background: #ffffff;
}

span.order-cancel {
  font-size: 18px;
}

span.span-order{
  color: #00acc1;
  cursor: pointer;
}

img.img-menu{
  width: 100%;
  border-radius: 5px;
}
</style>




