<template>
  <div id="content">
    <table id="sale_table">
      <col width="80%">
      <col width="15%">
      <col width="15%">
      <col width="10%">
      <thead>
        <tr>
          <th>รายการ</th>
          <th>ราคา/บาท</th>
          <th>กิจกรรม</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in listorder.data" :key="item.data">
          <td>{{item.orders}}</td>
          <td>{{item.prices}}</td>
          <div id="b-active">
            <button class="select" @click="seelct_order(item)">เลือก</button>
          </div>
        </tr>
      </tbody>
    </table>

    <div class="pagination">
      <a href="#">&laquo;</a>
      <a href="#">1</a>
      <a class="active" href="#">2</a>
      <a href="#">3</a>
      <a href="#">4</a>
      <a href="#">5</a>
      <a href="#">6</a>
      <a href="#">&raquo;</a>
    </div>
  </div>
</template>

<script>
export default {
  name: "practic",
  data() {
    return {
      listorder: [],
      select_count: 0
    };
  },
  methods: {
    itemList: function() {
      this.$http
        .get("http://localhost:1112/ezapi/api/get/getorder.php")
        .then(response => {
          //console.log(Response.body);
          this.listorder = response.body;
          //console.log(this.listorder);
        });
    },
    seelct_order: function(item) {
      this.select_count++;
      console.log(JSON.stringify(item));
      //console.log(this.select_count);
    }
  },
  created: function() {
    this.itemList();
  }
};
</script>

<style scoped>
#content {
  width: 100%;
  height: 100%;
  padding: 10px;
}

#sale_table {
  border: 1px solid #ddd;
  background: white;
  width: 100%;
  border-collapse: collapse;
}

#sale_table th {
  background-color: #888888;
  color: white;
  padding-bottom: 12px;
  padding-top: 12px;
  text-align: left;
}
#sale_table td,
#sale_table th {
  padding: 10px;
  border: 1px solid #ddd;
}
#sale_table tr:nth-child(even) {
  background-color: #f2f2f2;
}
#sale_table tr:hover {
  background-color: #ddd;
}

#b-active {
  width: 100%;
  padding-left: 10%;
  padding-right: 10%;
  padding-top: 3px;
}

button.select {
  width: 100%;
  height: 36px;
  padding: 6px 12px;
  color: #fff;
  background-color: rgb(131, 168, 238);
  border-color: rgb(26, 83, 189);
  border: 1px solid rgb(47, 102, 204);
  border-radius: 4px;
}

.pagination {
  text-align: right;
  width: 100%;
  display: inline-block;
  margin-top: 15px;
  margin-bottom: 0%;
  margin-right: 0%;
  margin-left: 0%;
}

.pagination a {
  color: black;
  padding: 0px 4px;
  text-decoration: none;
}

.pagination a.active {
  background-color: #4caf50;
  color: white;
}

.pagination a:hover:not(.active) {
  background-color: #ddd;
}
</style>



