<template>
  <div>
    <!-- nav bar  -->
    <div id="nav_content">
      <div id="nav">
        <span class="logo">EZQueue</span>
      </div>
      <div id="nav">
        <div id="navR">
          <i class="glyphicon glyphicon-log-out"></i>
        </div>
      </div>
    </div>
    <!-- nav bar  -->
  </div>
</template>

<script> 
export default {
  name: "navigration"
};
</script>

<style scoped>
#nav_content {
  line-height: 60px;
  width: 100%;
  height: 60px;
  background: #18976e;
}

#nav {
  float: left;
  /* padding-top: 20px; */
  padding-left: 5%;
  padding-right: 5%;
  width: 50%;
  color: white;
}

span.logo {
  font-size: 22px;
}

#navR {
  line-height: 70px;
  width: 100%;
  text-align: right;
}

.glyphicon-log-out {
  font-size: 20px;
}
</style>



