import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Practic from '@/components/Practic'
import Navigration from '@/components/Navigration'
import SaleOrder from '@/components/SaleOrder'
import Login from '@/components/Login'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'login',
      component: Login
    },
    {
      path: '/home',
      name: 'home',
      component: Home
    },
    {
      path: '/practic',
      name: 'practic',
      component: Practic
    },
    {
      path: '/navigration',
      name: 'navigration',
      component: Navigration
    },
    {
      path: '/saleorder',
      name: 'saleorder',
      component: SaleOrder
    }

  ]
})



