<template>
  <div>
    <media :query="{minWidth: 1025, maxWidth: 100000}">
      <div class="css_content">
        <div class="panel_defualt3">
          <div class="panel_body1">
            <a>EZQueue</a>
            <br>
            <br>
            <input type="email" placeholder="Email" id="username" value="yochida">
            <br>
            <br>
            <input type="password" placeholder="Password" id="password" value="2015">
            <br>
            <br>
            <button @click="onSubmit">Sign in</button>
            <br>
            <br>
            <div class="css_version">
              <a>VERSION 1.0.0</a>
            </div>
          </div>
        </div>
      </div>
    </media>

    <media :query="{minWidth: 500, maxWidth:1024}">
      <div class="css_content">
        <div class="panel_defualt2">
          <div class="panel_body1">
            <a>Welcome</a>
            <br>
            <br>
            <input type="email" placeholder="Email" id="username" value="yochida">
            <br>
            <br>
            <input type="password" placeholder="Password" id="password" value="2015">
            <br>
            <br>
            <button @click="onSubmit">Sign in</button>
            <br>
            <br>
            <div class="css_version">
              <a>VERSION 1.0.0</a>
            </div>
          </div>
        </div>
      </div>
    </media>

    <media :query="{minWidth: 0, maxWidth:499}">
      <div class="css_content">
        <div class="panel_defualt1">
          <div class="panel_body1">
            <a>Welcome</a>
            <br>
            <br>
            <input type="email" placeholder="Email" id="username" value="yochida">
            <br>
            <br>
            <input type="password" placeholder="Password" id="password" value="2015">
            <br>
