            <div class="css_version">
              <a>VERSION 1.0.0</a>
            </div>
          </div>
        </div>
      </div>
    </media>
  </div>
</template>

<script>
import Vue from "vue";
import Media from "vue-media";
import { resolve } from "path";

export default {
  name: "login",
  data() {
    return {
      listuser: []
    };
  },
  components: {
    Media
  },
  methods: {
    onSubmit: function() {
      var email = document.getElementById("username").value;
      var password = document.getElementById("password").value;

      this.$http
        .post("http://localhost:1112/ezapi/api/post/getuser.php", {
          username: email,
          password: password
        })
        .then(
          response => {
            //console.log(response.body);
            this.listuser = response.body;
            //console.log(this.listuser.st);
            if (this.listuser.st == "true") {
              Vue.notify({
                group: "foo",
                type: 'success',
                title: "Message",
                text: "เข้าสู่ระบบ",
                duration: 1000,
                speed: 500
              });
              this.$router.push("/home");
            } else {
              Vue.notify({
                group: "foo",
                type: 'warn',
                title: "Message",
                text: this.listuser.message,
                duration: 1000,
                speed: 500
              });
            }
          },
          response => {
            this.listuser = [];
          }
        );

      //this.$router.push("/home");
    }

    // getLogin: function() {
    //   this.$http.get("http://localhost:1112/API/api/post/read.php").then(
    //     response => {
    //       console.log(response);
    //       this.list = response.body;
    //     },
    //     () => {
    //       this.list = [];
    //     }
    //   );
    // },

    // getLoinUrl: function() {
    //   this.$http
    //     .post(
    //       "http://localhost:1112/API/api/post/getuser.php?username=" + "yochida"
    //     )
    //     .then(
    //       response => {
    //         console.log(response);
    //         this.listurl = response.body;
    //       },
    //       response => {
    //         this.listurl = [];
    //       }
    //     );
    // },
  },
  created: function() {
    // this.getLogin();
    // this.getLoinUrl();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.css_content {
  float: left;
  width: 100%;
  height: fit-content;
  color: rgb(41, 41, 41);
  text-align: center;
  position: absolute;
  top: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}
/* panel of media screen min 1025*/
.panel_defualt3 {
  padding-right: 35%;
  padding-left: 35%;
}
/* panel of media screen min 500*/
.panel_defualt2 {
  padding-right: 15%;
  padding-left: 15%;
}
/* panel of media screen min 0*/
.panel_defualt1 {
  padding-right: 5%;
  padding-left: 5%;
}
.panel_body1 {
  width: 100%;
  height: fit-content;
  background: rgb(255, 255, 255);
  padding: 40px 40px;
  /* border-radius: 2%;
  box-shadow: 2px 6px #adadad; */
}
.panel_body1 a {
  color: dimgrey;
  font-size: 22px;
  text-decoration: unset;
}
.panel_body1 input {
  width: 100%;
  height: 36px;
  padding: 6px 12px;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.panel_body1 button {
  width: 100%;
  height: 36px;
  padding: 6px 12px;
  color: #fff;
  background-color: #1cad7e;
  border-color: #18976e;
  border: 1px solid #ccc;
  border-radius: 3px;
}
.css_version a {
  font-size: 12px;
}
</style>



